import { mapActions, mapGetters } from 'vuex';
import Moment from 'moment';
import icons from '../icons';

export default {
  name: 'LastSeenList',
  data() {
    return {
      filter: '',
      loading: false,
      updateInterval: null,
      pagination: {
        sortBy: 'desc',
        descending: false,
        rowsPerPage: this.$q.screen.xs ? 8 : 12,
      },
    };
  },
  methods: {
    ...mapActions('tools', ['getLastSeen']),
  },
  mounted() {
    this.loading = true;
    this.getLastSeen()
      .finally(() => {
        this.loading = false;
      });
    this.updateInterval = setInterval(() => {
      this.getLastSeen();
    }, 30000);
  },
  destroyed() {
    clearInterval(this.updateInterval);
  },
  computed: {
    ...mapGetters('tools', ['lastSeen']),
    icons() {
      return icons;
    },
    humanLastSeen() {
      return this.lastSeen.map((value) => {
        const humanReadable = Moment.utc(value.date).local().format('Do MMM YYYY, h:mm a');
        return { user: value.user, date: humanReadable, never: value.never };
      });
    },
  },
};
